.page {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__hand {
  transform-origin: 0 100%;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: rotate(0)translateX(-50%);
}

.stopwatch__hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotates;
}

.stopwatch__hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotates;
}

@keyframes rotates {
  from {
    transform: rotate(0)translateX(-50%);
  }

  to {
    transform: rotate(360deg)translateX(-50%);
  }
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}
